import { Action } from './Action';

export class ShowAction extends Action {
    /**
     * Affiche les dtails d'une entit dans un modal.
     * @param {number|string} id - Identifiant de l'entit  afficher.
     */
    showEntity(id) {
        const showUrl = this.getUrlWithId(this.config.showUrl, id); // Gnrer l'URL dynamique

        // Afficher le chargement dans le modal
        this.modalManager.showLoading('Dtails de l\'entit');

        // Charger les dtails de l'entit via AJAX
        $.get(showUrl)
            .done((html) => {
                // Injecter le contenu des dtails dans le modal
                this.modalManager.showContent(html);
                this.formManager.init();
                this.formManager.setToReadOnly();
                // this.handleSuccess('Dtails de l\'entit chargs avec succs.');
            })
            .fail((xhr) => {
                const errorMessage = xhr.responseJSON?.message || 'Erreur lors du chargement des dtails de l\'entit.'
                this.handleError(errorMessage);
            });
            
    }
}
