import { LoadingIndicator } from '../components/LoadingIndicator';
import { NotificationHandler } from '../components/NotificationHandler';
import { Action } from './Action';
import { BaseAction } from './BaseAction';

// LoadListAction ne peut pas hrite de Action
// pour viter une dpendance circulaire des imports
export class LoadListAction extends BaseAction {
    /**
     * Constructeur de LoadListAction.
     * @param {Object} config - Configuration pour le chargement des entits.
     */
    constructor(config) {
        super(config);
        this.indexUrl = this.appendParamsToUrl(
            config.indexUrl,
            this.contextManager.getContextParams()
        );
    }

    /**
     * Charge les entits depuis le serveur et met  jour la table ou la liste.
     * @param {number} page - Numro de la page  charger (par dfaut : 1).
     * @param {string} q - Valeur de recherche pour filtrer les entits.
     */
    loadEntities(page , q ) {


        // Extraire les paramtres de l'URL si les arguments sont nuls
        const urlParams = new URLSearchParams(window.location.search);
        const _page = urlParams.get('page'); // "2"
        const _query = urlParams.get('q');   // "test"

        page = page || _page || 1;
        q = q || _query || '';

      
        const search_params = `page=${page}&q=${q}`;

        this.indexUrl = this.appendParamsToUrl(
            this.indexUrl, search_params
        );

        // Afficher l'indicateur de chargement
        this.loader.show();

        // Requte AJAX pour charger les entits
        $.get(this.indexUrl)
            .done((html) => {
                // Mettre  jour le contenu de la table ou liste
                $(this.config.tableSelector).html(html);

                // Afficher un message de succs
                // NotificationHandler.showSuccess('Donnes charges avec succs.');
            })
            .fail(() => {
                // Grer les erreurs
                NotificationHandler.showError('Erreur lors du chargement des donnes.');
            })
            .always(() => {
                // Masquer l'indicateur de chargement
                this.loader.hide();
            });
    }
}
