import { NotificationHandler } from '../components/NotificationHandler';

export class SearchPaginationEventHandler {
    /**
     * Constructeur de SearchPaginationEventHandler.
     * @param {Object} config - Configuration contenant les slecteurs et URLs.
     * @param {Object} entityLoader - Instance de LoadListAction pour recharger les entits.
     */
    constructor(config, entityLoader) {
        this.config = config;
        this.entityLoader = entityLoader;

        // Temps de dlai pour la recherche (debounce)
        this.debounceTimeout = null;
        this.debounceDelay = 500; // Par dfaut : 500ms
    }

    /**
     * Initialise les gestionnaires pour la recherche et la pagination.
     */
    init() {
        this.handleSearchInput();
        this.handlePaginationClick();
    }

    /**
     * Gre les vnements de recherche avec un dlai pour limiter les requtes frquentes.
     */
    handleSearchInput() {
        $(document).on('keyup', this.config.searchInputSelector, (e) => {
            const searchValue = $(e.currentTarget).val();
            this.updateURLParameter('q', searchValue);
            clearTimeout(this.debounceTimeout); // Rinitialiser le dlai prcdent
            this.debounceTimeout = setTimeout(() => {
                this.entityLoader.loadEntities(1, searchValue); // Recharger les entits avec la valeur de recherche
                NotificationHandler.showInfo('Recherche en cours...');
            }, this.debounceDelay);
        });
    }

    /**
     * Gre les clics sur les liens de pagination.
     */
    handlePaginationClick() {
        $(document).on('click', this.config.paginationSelector, (e) => {
            e.preventDefault();
            const page = $(e.currentTarget).data('page') || $(e.currentTarget).attr('data-page') || $(e.target).text().trim();

            // const page = $(e.currentTarget).data('page') || $(e.target).text().trim();

            if (page) {
                const searchValue = $(this.config.searchInputSelector).val(); // Obtenir la valeur actuelle de la recherche
                this.updateURLParameter('page', page); // Met  jour l'URL
                this.entityLoader.loadEntities(page, searchValue); // Charger la page demande
                NotificationHandler.showInfo(`Chargement de la page ${page}...`);
            }
        });
    }

    /**
     * Met  jour un paramtre dans l'URL sans recharger la page.
     * @param {string} param - Nom du paramtre  mettre  jour.
     * @param {string|number} value - Valeur du paramtre.
     */
    updateURLParameter(param, value) {
        const url = new URL(window.location.href);

        if (value === undefined || value === null || value === '') {
            url.searchParams.delete(param); // Supprime le paramtre s'il n'y a pas de valeur
        } else {
            url.searchParams.set(param, value); // Met  jour ou ajoute le paramtre
        }

        // Met  jour l'URL dans la barre d'adresse sans recharger la page
        window.history.replaceState({}, '', url);
    }
}
